import {inject, noView, BindingEngine, bindable} from 'aurelia-framework'; // or 'aurelia-binding'

@inject(BindingEngine)
@noView()
export class MultiObserver {
  @bindable temp = [1,2]
  subscription;

  constructor(private bindingEngine) {

  }

  observe(properties, callback) {
    let subscriptions = <any> [];
    let i = properties.length;
    let obs = this.bindingEngine
        .collectionObserver(this.temp)
        .subscribe(splices => this.changed(splices));

    subscriptions.push(obs);
    // while(i--) {
    //     let obs = this.bindingEngine
    //         .collectionObserver(properties[i])
    //         .subscribe(() => console.log("Test"));
    //
    //     subscriptions.push(obs);
    // }

    // return dispose function
    return () => {
      while(subscriptions.length) {
        subscriptions.pop().dispose();
      }
    }
  }

  changed(s) {
      console.log("asdasdasd")
  }

  sub() {
      console.log("Sub")
      if (this.subscription) {
        this.subscription.dispose();
      }
      this.subscription = this.bindingEngine
        .collectionObserver(this.temp)
        .subscribe(splices => {
          this.changed(splices);
        });
  }

  tempChanged() {
      console.log("Array changed")
  }



  test() {
      console.log("Test")
      console.log(this.temp)
      this.temp = [1,4];
      console.log(this.temp)
      this.temp = [2,3]
      console.log(this.temp)
  }
}
